/**
 * http
 */
// axioselement uiloadingmessage
import axios from 'axios'
import { Loading, Message } from 'element-ui'
import router from './router'
axios.defaults.baseURL = 'http://192.168.1.15:8081'
axios.defaults.withCredentials = true
// axios
let httpi = axios.create({
  baseURL: 'http://192.168.1.15:8081',
  withCredentials: true
})

// http
var loadinginstace
httpi.interceptors.request.use(config => {
  // element ui Loading
  loadinginstace = Loading.service({ fullscreen: true })
  return config
}, error => {
  loadinginstace.close()
  Message.error({
    message: 'time out'
  })
  return Promise.reject(error)
})
// http
httpi.interceptors.response.use(response => {// loading
  loadinginstace.close()
  return response
}, error => {
  loadinginstace.close()
  console.log(error);
  router.push({name: 'Error'})
  // return Promise.reject(error)
})

export {axios,httpi}


